<template>
  <div class="basic-layout">
    <a-layout>
      <a-layout-header class="header">
        <GlobalHeader @show-modal="showModal" />
      </a-layout-header>

      <a-layout-content class="content">
        <HomeView />
      </a-layout-content>

      <a-layout-footer class="footer">
        <a href="https://starrysky.szzz666.top/"
          >星空玩家行为记录器 by 星空服务器</a
        ></a-layout-footer
      >
    </a-layout>
    <a-modal
      class="modal"
      width="300px"
      v-model:open="open"
      title="身份验证"
      @ok="handleOk"
      @cancel="handleCancel"
      okText="登录"
      :cancelButtonProps="{ style: { display: 'none' } }"
    >
      <a-input class="input" placeholder="用户名" v-model:value="username" />
      <a-input
        class="input"
        placeholder="密码"
        v-model:value="password"
        type="password"
      />
    </a-modal>
  </div>
</template>

<script setup lang="js">
import GlobalHeader from "@/components/GlobalHeader.vue";
import HomeView from "@/views/HomeView.vue";
import {inject, onMounted, ref} from "vue";
import axios from "axios";
import {message} from "ant-design-vue";
import { eventBus } from '@/eventBus.js';
onMounted(() => {
  if (isCanLogin()) {
    login();
  } else {
    showModal();
  }
});
onMounted(() => {
  eventBus.on('show_login_modal', showModal);
});

// 暴露方法
const open = ref(false);
const username = ref("");
const password = ref("");

function showModal() {
  open.value = true;
}

function handleOk() {
  open.value = false;
  login();
}

function handleCancel() {
  open.value = false;
  if (!isCanLogin()) {
    message.error("请先登录！");
    // 一秒后
    setTimeout(() => {
      showModal();
    }, 500);
  }
}

const url = inject('url');

function login() {
  if (!isCanLogin()) {
    localStorage.setItem("username", username.value);
    localStorage.setItem("password", password.value);
  }
  axios.post(url + "/login", {
    username: localStorage.getItem("username"),
    password: localStorage.getItem("password")
  }).then(res => {
    if (res.data.code === 200) {
      handleCancel();
      message.success(res.data.msg);
      eventBus.emit('sendDataRequest')
      return;
    }
    message.error(res.data.msg);
    localStorage.removeItem("username");
    localStorage.removeItem("password");
    showModal();
  }).catch(error => {
    console.log(error);
    message.error(error);
  });

}

function isCanLogin() {
  const username = localStorage.getItem("username");
  const password = localStorage.getItem("password");
  return username && password;
}
</script>

