<template>
  <div class="home">
    <a-table
      :dataSource="data"
      :columns="columns"
      :pagination="pagination"
      :loading="loading"
      :wrap="false"
      @change="handleTableChange"
    >
      <template #bodyCell="{ column, record }">
        <template v-if="column.key === 'action'">
          <a-button block @click="showDetail(record)">详细</a-button>
        </template>
      </template>
    </a-table>
    <a-modal
      v-model:visible="modalVisible"
      title="详细信息"
      :width="900"
      :okButtonProps="{ style: { display: 'none' } }"
      :cancelButtonProps="{ style: { display: 'none' } }"
    >
      <pre>{{ selectedRecord }}</pre>
    </a-modal>
    <a-modal
      v-model:visible="smodalVisible"
      title="筛选条件"
      :width="900"
      okText="确定"
      cancelText="取消"
      @ok="handleFilter"
    >
      <a-form
        :model="params"
        name="basic"
        :label-col="{ span: 4 }"
        :wrapper-col="{ span: 16 }"
        autocomplete="off"
      >
        <a-form-item label="ID" name="id">
          <a-input v-model:value="params.id" placeholder="id" type="number" />
        </a-form-item>
        <a-form-item label="时间" name="time">
          <a-input v-model:value="params.time" placeholder="time" />
        </a-form-item>
        <a-form-item label="玩家名称" name="player_name">
          <a-input
            v-model:value="params.player_name"
            placeholder="player_name"
          />
        </a-form-item>
        <a-form-item label="玩家uuid" name="player_uuid">
          <a-input
            v-model:value="params.player_uuid"
            placeholder="player_uuid"
          />
        </a-form-item>
        <a-form-item label="玩家位置" name="player_pos">
          <a-input v-model:value="params.player_pos" placeholder="player_pos" />
        </a-form-item>
        <a-form-item label="事件位置" name="event_pos">
          <a-input v-model:value="params.event_pos" placeholder="event_pos" />
        </a-form-item>
        <a-form-item label="事件方块" name="event_block">
          <a-input
            v-model:value="params.event_block"
            placeholder="event_block"
          />
        </a-form-item>
        <a-form-item label="事件物品" name="event_item">
          <a-input v-model:value="params.event_item" placeholder="event_item" />
        </a-form-item>
        <a-form-item label="其他信息" name="event_other">
          <a-input
            v-model:value="params.event_other"
            placeholder="event_other"
          />
        </a-form-item>
        <a-form-item label="事件类型" name="event_type">
          <a-input v-model:value="params.event_type" placeholder="event_type" />
        </a-form-item>
      </a-form>
    </a-modal>
  </div>
</template>

<script setup lang="js">
import {inject, onMounted, ref} from "vue";
import axios from "axios";
import {message} from "ant-design-vue";
import {eventBus} from '@/eventBus.js';

onMounted(() => {
  eventBus.on('showFilter', showFilter);
  eventBus.on('sendDataRequest', sendDataRequest);
});


const url = inject('url');

function sendDataRequest() {
  loading.value = true; // 开始加载时设置为 true
  axios.post(url + "/data", params.value, {
    headers: {
      "Authorization": "Basic " + btoa(localStorage.getItem("username") + ":" + localStorage.getItem("password")),
    }
  }).then(res => {
    data.value = res.data.data;
    pagination.value.total = res.data.total;
  }).catch(error => {
    console.log(error);
    message.error(error);
  }).finally(() => {
    loading.value = false; // 加载完成后设置为 false
  });
}

function handleTableChange(pag) {
  pagination.value.current = pag.current;
  params.value.current = pag.current;
  pagination.value.pageSize = pag.pageSize;
  params.value.pageSize = pag.pageSize;
  sendDataRequest();
}

function showDetail(record) {
  selectedRecord.value = record;
  modalVisible.value = true;
}

function showFilter() {
  smodalVisible.value = true;
}

function handleFilter() {
  smodalVisible.value = false;
  sendDataRequest();
}

const loading = ref(false); // 添加 loading 状态
const pagination = ref({
  total: 20,
  pageSize: 20,
  current: 1,
  showSizeChanger: true,
  pageSizeOptions: ['10', '20', '50', '100'],
  showQuickJumper: true,
  // 显示总数
  showTotal: (total) => `共 ${total} 条`,

});

const params = ref({
  current: pagination.value.current,
  pageSize: pagination.value.pageSize,
  id: -1,
  time: '',
  player_name: '',
  player_uuid: '',
  player_pos: '',
  event_pos: '',
  event_block: '',
  event_item: '',
  event_other: '',
  event_type: ''
});
const columns = ref([
  {
    title: 'ID',
    dataIndex: 'id',
    width: 64,
    ellipsis: true,
  },
  {
    title: '时间',
    dataIndex: 'time',
    width: 230,
    ellipsis: true,
  },
  {
    title: '玩家名称',
    dataIndex: 'playerName',
    width: 120,
    ellipsis: true,
  },
  {
    title: '玩家uuid',
    dataIndex: 'playerUUID',
    ellipsis: true,
  },
  {
    title: '玩家位置',
    dataIndex: 'playerPos',
    ellipsis: true,

  },
  {
    title: '事件位置',
    dataIndex: 'eventPos',
    ellipsis: true,
  },
  {
    title: '事件方块',
    dataIndex: 'eventBlock',
    ellipsis: true,
  },
  {
    title: '事件物品',
    dataIndex: 'eventItem',
    ellipsis: true,
  },
  {
    title: '其他信息',
    dataIndex: 'eventOther',
    ellipsis: true,
  },
  {
    title: '事件类型',
    dataIndex: 'eventType',
    width: 140,
    ellipsis: true,
  },
  {
    title: '操作',
    key: 'action',
    width: 100,
    ellipsis: true,
  }
]);

const data = ref([
  {
    id: 1,
    time: '2022-01-01 12:00:00',
    playerName: 'playerName',
    playerUUID: 'playerUUID',
    playerPos: 'playerPos',
    eventPos: 'eventPos',
    eventBlock: 'eventBlock',
    eventItem: 'eventItem',
    eventOther: 'eventOther',
    eventType: 'eventType'
  }
]);
const modalVisible = ref(false);
const selectedRecord = ref(null);
const smodalVisible = ref(false);
</script>
<style lang="css" scoped></style>
